___CSS_LOADER_EXPORT___.push([module.id, `html {
  --black: #000;
  --dark-gray: #181818;
  --gray: #383838;
  --light-gray: #555;
  --white: #ccc;

  --blue: #339;
  --light-blue: #66f;
  --pale-blue: #559;
  --less-pale-blue: #449;

  --red: #c44;
  --pale-red: #f88;

  --color-bg-primary: var(--dark-gray);
  --color-text-primary: var(--white);
  --color-text-error: var(--pale-red);

  --color-main-border: var(--gray);
  --color-container-box-shadow: var(--black);

  --color-field-bg: var(--black);
  --color-field-text: var(--white);
  --color-field-border: var(--light-gray);
  --color-field-border-focus: var(--light-blue);
  --color-field-border-error: var(--red);

  --color-button-bg: var(--blue);
  --color-button-bg-hover: var(--less-pale-blue);
  --color-button-text: var(--white);
  --color-button-border: var(--pale-blue);
  --color-button-border-focus: var(--light-blue);

  --color-loading-indicator: var(--light-blue);
}
